<template>
	<view class="container">		
		<form @submit="onSubmit">
			<view class="item">
				<input v-model="formValue.title" type="text" name="title" placeholder="请输入标题"></input>
			</view>
			
			<view class="item">
				<input v-model="formValue.author" type="text" name="author" placeholder="请输入作者"></input>
			</view>
			
			<view class="item">
				<textarea v-model="formValue.content" name="content" placeholder="请输入详细内容"></textarea>
			</view>
			
			<view class="item">
				<uni-file-picker 
					:auto-upload="false"
					v-model="imageValue" 
					fileMediatype="image" 
					mode="grid"
					 					
				/>
			</view>
			
			<view class="item">
				<button form-type="reset">重置</button>
				<button form-type="submit" type="primary" :disabled="inDisabled">提交</button>
			</view>
			
		</form>
	</view>
</template>

<script>
	let id;
	export default {
		data() {
			return {
				picurls:[],
				imageValue:[],
				formValue:{
					title:"",
					author:"",
					content:"",	
				}
			};
		},
		onLoad(e){			
			id=e.id;
			uni.setNavigationBarTitle({
				title:id ? '修改文章' : '新增文章'
			})
			this.getDetail();
		},
		computed:{
			//判断按钮是否禁用
			inDisabled(){
				let {title,author,content}= this.formValue
				let obj = {title,author,content}
			 	return Object.keys(obj).some((key)=>{
					return this.formValue[key]==''
				})
			}
		},	
		
		methods:{
			//图片上传成功
		// 	uploadSuccess(e){				
		// 		this.picurls =e.tempFilePaths
		
		// 		console.log(e)			
		// 	},
			
			//获取详情
			getDetail(){
				if(id){
					uniCloud.callFunction({
						name:"art_get_row",
						data:{
							id
						}
					}).then(res=>{					
						this.formValue=res.result.data[0]
						console.log(this.formValue)
						console.log(res)
						if(!this.formValue.picurls) return;
						let urls=this.formValue.picurls.map(item=>{
							return {url:item}
						})
						this.imageValue=urls;
					})
				} else return
			},						
			//点击提交
			onSubmit(){
				let _picurls=this.imageValue.map(item=>{
					return item.url;
					})
				if(id){								
					uniCloud.callFunction({
						name:"art_update_row",
						data:{
							detail:this.formValue,
							picurls:_picurls
						}
					}).then(res=>{
						uni.showToast({						
							title:"修改成功"
						})
						setTimeout(()=>{
							uni.navigateBack()
						},800)
					})
				} else {
					
					uniCloud.callFunction({
						name:"art_add_row",
						data:{
							detail:this.formValue,
							picurls:_picurls
						}
					}).then(res=>{
						
						uni.showToast({
							title:"发布成功"
						})
						
						setTimeout(()=>{
							uni.reLaunch({
								url:"/pages/index/index"
							})
						},800)
					})
				}
			
			}
		}
	}
</script>

<style lang="scss" scoped>
.container{
	padding:30rpx;
	.item{
		padding-bottom:20rpx;
		input,textarea{
			border:1rpx solid #eee;
			height: 80rpx;
			padding:0 20rpx;
		}
		textarea{
			height: 200rpx;
			width: 100%;
			box-sizing: border-box;
		}
		button{
			margin-bottom:20rpx;
		}
	}
}
</style>
